import { createWidget, widget } from './base';
import { AbstractWidget } from './widget';
@widget('bi.checkbox')
export class CheckBox extends AbstractWidget {
    public constructor(props: any) {
        super(props);
    }

    public getChildren() {
        return [
            createWidget({
                type: 'bi.widget',
                width: 5,
                height: 8,
                left: this.left,
                top: this.top,
                style: {
                    rotate: '45',
                    borderBottom: '2px solid #000',
                    borderRight: '2px solid #000',
                },
            }),
        ];
    }
}
